.profile__add-button {
  width: 150px;
  height: 50px;
  border: 2px solid white;
  display: flex;
  box-sizing: border-box;
  border-radius: 2px;
  background: url(../../../images/vector.svg) center  center / 22px 22px no-repeat;
  padding: 0;
}

@media (max-width: 700px) {
  .profile__add-button {
    width: 282px;
    background-size: 16px 16px;
  }
}
