.profile__edit-button {
  width: 24px;
  height: 24px;
  background: url(../../../images/edit_button.svg) center no-repeat;
  padding: 0;
  border: 1px solid white;
  cursor: pointer;
  grid-column: 2/3;
  grid-row: 1/2;
  align-self: center;
}

@media (max-width: 700px) {
  .profile__edit-button {
    width: 18px;
    height: 18px;
  }
}
