.profile__title {
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 1.14;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 294px;
}

@media (max-width: 550px) {
  .profile__title {
    font-size: 27px;
    line-height: 1.22;
    max-width: 189px;
  }
}
