.popup__container {
  max-width: 430px;
  background-color: #fff;
  margin: 0 auto;
  border-radius: 10px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  padding: 34px 36px 37px;
  box-sizing: border-box;
}

@media (max-width: 550px) {
  .popup__container {
    width: 282px;
    padding: 25px 22px;
  }
}
