.popup__input {
  border: none;
  width: 100%;
  min-height: 27px;
  padding: 0;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  font-weight: 400;
  font-size: 14px;
  line-height: 1.21;
}

.popup__input:nth-of-type(2){
  margin-bottom: 48px;
}

@media (max-width: 550px) {
  .popup__input:nth-of-type(2) {
    margin-bottom: 45px;
  }
}
